import * as React from 'react';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import Button from '@mui/material/Button';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import MenuIcon from '@mui/icons-material/Menu';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import { alpha, CssBaseline, Grid, InputBase, Link, styled } from '@mui/material';
import Routing from '../Routing/Routing';
import Footer from '../Footer/Footer';
import './MenuDrawer.css'
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import SearchIcon from '@mui/icons-material/Search';
import AutoComplete from '../../ShareArea/AutoComplete/AutoComplete';

// type string = 'right';

export default function TemporaryDrawer() {
  const [state, setState] = React.useState({
    right: false
  }
  );

  const toggleDrawer =
    (anchor:string, open: boolean) =>
    (event: React.KeyboardEvent | React.MouseEvent) => {
      if (
        event.type === 'keydown' &&
        ((event as React.KeyboardEvent).key === 'Tab' ||
          (event as React.KeyboardEvent).key === 'Shift')
      ) {
        return;
      }

      setState({ ...state, [anchor]: open });
    };

  const list = (anchor: string) => (
    <Box
      sx={{ width: 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        <ListItem>
        <Link href='/home' sx={{ color: 'black', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>דך הביתה </Link>
            
        </ListItem>
        <ListItem>
        <Link href='/products' sx={{ color: 'black', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>מוצרים </Link>

        </ListItem>
        <ListItem>
        <Link href='/about-us' sx={{ color: 'black', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>אודות </Link>
            
        </ListItem>
        <ListItem>
        <Link href='/home' sx={{ color: 'black', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>חדשים </Link>
            
        </ListItem>
        <ListItem>
        <Link href='/contact-us' sx={{ color: 'black', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>צור קשר </Link>
            
        </ListItem>
        <ListItem>
        <Link href='/admin-page' sx={{ color: 'black', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>צור קשר </Link>
            
        </ListItem>
      </List>
    </Box>
  );

  const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginRight: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: '450px',
    },
  }));
  
  const SearchIconWrapper = styled('div')(({ theme }) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  }));
  
  
  
 
  
  return (
    <div className='MenuDrawer'>
      {([ 'right'] as const).map((anchor) => (
          <React.Fragment key={anchor}>
              <Grid container >
                  <Grid item xs={12}>
                      <AppBar sx={{ background: 'black' }}>
                          <Toolbar>
                              <Button onClick={toggleDrawer(anchor, true)} sx={{ display: 'none' }} className='MobileButton'><MenuIcon /></Button>
                              <Drawer
                                  anchor={anchor}
                                  open={state[anchor]}
                                  onClose={toggleDrawer(anchor, false)}
                              >
                                  {list(anchor)}
                              </Drawer>
                              <CssBaseline />
                              <Grid container className='LinksDisplay' sx={{ display: 'flex', justifyContent: 'center' }} item xs ={2} sm ={8}>
                                  <Grid>
                                      <Link href='/home' sx={{ color: 'white', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>דך הביתה </Link>
                                  </Grid>
                                  <Grid>
                                      <Link href='/products' sx={{ color: 'white', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>מוצרים </Link>
                                  </Grid>
                                  <Grid>
                                      <Link href='/about-us' sx={{ color: 'white', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>אודות </Link>
                                  </Grid>
                                  <Grid>
                                      <Link href='/home' sx={{ color: 'white', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>חדשים </Link>
                                  </Grid>
                                  <Grid>
                                      <Link href='/contact-us' sx={{ color: 'white', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>צור קשר </Link>
                                  </Grid>
                                  <Grid>
                                      <Link href='/admin-page' sx={{ color: 'white', textDecoration: 'none', fontFamily: 'Arial', fontSize: '20px', padding: '10px' }}>כניסה למערכת </Link>
                                  </Grid>
                                  </Grid>
                                  <Grid>
                                    
                                  <Grid container item xs ={10} sm={4}>
                                      <Search>
                                          <SearchIconWrapper>
                                              <SearchIcon />
                                          </SearchIconWrapper>
                                          <AutoComplete />
                                      </Search>
                                  </Grid>
                                  </Grid>

                              

                          </Toolbar>
                      </AppBar>
                  </Grid>
                  <Grid item sm={2} />
                  <Grid item xs={12} sm={8} sx={{ marginBottom: '50px', marginTop: '100px' }}>
                      <Routing />
                  </Grid>
                  <Grid sm={2} />
              </Grid>
          </React.Fragment>
      ))}
        </div>
    );
}
