abstract class Config {
    public urls = {
        product:"",
        login:"",
        api:"",
        cart :"",
        forum:"",
        images:"",
        clocks:"",
        chains:"",
        rings:"",
        earings:"",
        bracelets:"",
        similar:"",

    };
   public constructor(Baseurl:string) {
       this.urls = 
       {
        api:Baseurl+"",
        product:Baseurl + "products/",
        login:Baseurl + "login/",
        cart:Baseurl + "cart/",
        forum:Baseurl + "forum/",
        images:Baseurl + "images/",
        clocks:Baseurl+ "clocks/",
        rings:Baseurl +"products/rings/",
        earings:Baseurl +"products/earings/",
        bracelets:Baseurl +"products/bracelets/",
        chains:Baseurl +"products/chains/",
        similar :Baseurl +"products/similar",
        


       };
    }       
}
class DevelopConfig extends Config{
    public constructor(){
        super("/api/");
    }

}
class ProductionConfig extends Config{
    public constructor(){
        super("/api/");
    }
}
const config = process.env.NODE_ENV ==="development"? new DevelopConfig():new ProductionConfig();
export default config;

