import * as React from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';

export default function AddressForm(props) {
  const userDetails =[]
    props.parentCallback(userDetails)
// console.log(userDetails)
  return (
    <React.Fragment >
      <Typography variant="h6" gutterBottom>
        כתובת משלוח
      </Typography>
      <Grid container spacing={3}>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="שם פרטי"
            name="firstName"
            label="שם פרטי"
            fullWidth
            autoComplete="given-name"
            variant="filled"
            onChange={(event)=>{userDetails.push(event.target.value)}}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="שם משפחה"
            name="lastName"
            label="שם משפחה"
            fullWidth
            autoComplete="family-name"
            variant="filled"
            onChange={(event)=>{userDetails.push(event.target.value)}}

          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            required
            id="כתובת"
            name="address1"
            label="כתובת 1"
            fullWidth
            autoComplete="shipping address-line1"
            variant="filled"
            onChange={(event)=>{event.preventDefault(); userDetails.push(event.target.value)}}

          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            id="כתובת2"
            name="address2"
            label="כתובת2"
            fullWidth
            autoComplete="shipping address-line2"
            variant="filled"
            onChange={(event)=>{event.preventDefault();userDetails.push(event.target.value)}}

          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="עיר"
            name="city"
            label="עיר"
            fullWidth
            autoComplete="shipping address-level2"
            variant="filled"
            onChange={(event)=>{userDetails.push(event.target.value)}}

          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            id="עיר"
            name="state"
            label="עיר"
            fullWidth
            variant="filled"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="מיקוד"
            name="zip"
            label="מיקוד"
            fullWidth
            autoComplete="shipping postal-code"
            variant="filled"
            onChange={(event)=>{userDetails.push(event.target.value)}}

          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <TextField
            required
            id="מדינה"
            name="country"
            label="מדינה"
            fullWidth
            autoComplete="shipping country"
            variant="filled"
            onChange={(event)=>{userDetails.push(event.target.value)}}

          />
        </Grid>
        <Grid item xs={12}>
          <FormControlLabel
            control={<Checkbox color="secondary" name="saveAddress" value="yes" />}
            label="שמור כתובת לרכישה הבאה"
          />
        </Grid>
      </Grid>
    </React.Fragment>
  );
}