import * as React from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete, { createFilterOptions } from '@mui/material/Autocomplete';
import productModel from '../../../Modules/productModel';
import axios from 'axios';
import config from "../../../Configs/Configs"
import  "./AutoComplete.css"
import { useState } from 'react';
import { useEffect } from 'react';
import { cleanup } from '@testing-library/react';

export default function AutoComplete() {
  const [products,setProducts] =useState<productModel[] | null >()
  useEffect(()=>{
      axios.get(config.urls.product).then((res)=>{setProducts(res.data)}).
      catch((err)=>{return err})
      console.log('SAS')

  },[])
  return (
    <Autocomplete
       
      selectOnFocus
      clearOnBlur
      handleHomeEndKeys
      id="free-solo-with-text-demo"
      options={products?.map((option)=>option.id) ? products : []}
      getOptionLabel={(option) => {
        // Value selected with enter, right from the input
        if (typeof option === 'string') {
          return option;
        }
        
        // Regular option
        return option?.name;
      }}
      renderOption={(props, option) => <div className ="RenderOption" key ={option.id}><li {...props} key={option.id}><a key ={option.id} href ={"/products/buy/"+option?.image + "&&" + option?.id}>{option.name}</a></li></div>}
      sx={{width:360}}
      
      freeSolo
      renderInput={(params) => (
        <TextField {...params} label="חיפוש" variant="filled" />
      )}
    />
  );
}


