import { AppBar, Box, Button, Container, createTheme, CssBaseline,  Paper, Step, StepLabel, Stepper, ThemeProvider, Toolbar, Typography } from "@material-ui/core";
import axios from "axios";
import React, { useEffect } from "react";
import { useState } from "react";
import { RouteComponentProps } from "react-router";
import productModel from "../../../Modules/productModel";
import AddressForm from "./AddressForm";
import PaymentForm from "./PaymentForm";
import Review from "./Review";
import config from "../../../Configs/Configs";
import Grid from '@mui/material/Grid'
const steps = ['כתובת משלוח', 'תשלום', 'סיכום הזמנה'];
let addressFormDetails =[];
interface RouterParams {
  id: string;
}
interface checkOutProps extends RouteComponentProps<RouterParams> {}

function getStepContent(step: number) {
  switch (step) {
    case 0:
      return <AddressForm parentCallback ={addressFormCallback}/>;
    case 1:
      return <PaymentForm />;
    case 2:
      return <Review addressFromDetails ={addressFormDetails}/>;
    default:
      throw new Error('Unknown step');
  }
}
const addressFormCallback =(userDetails)=>{
    addressFormDetails.push(userDetails)
 }
export default function CheckOut(props:checkOutProps){
  const theme = createTheme();
  const [activeStep, setActiveStep] =useState(0);
  let [product,setProduct] = useState<productModel | null>(null)
  useEffect(()=>{
    addressFormDetails.push(props.match.params.id)
    axios.get(config.urls.product + props.match.params.id)
    .then((res)=>{setProduct(res.data)})
    .catch((err)=>{alert(err.message)})
   
  },[])
  const handleNext = () => {
    setActiveStep(activeStep + 1);
  };

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

    return(
    <ThemeProvider theme ={theme}>
   
    <Container component='main' maxWidth='md'>
        <Grid container>
         <Grid item xs ={12} sx ={{display:'flex' ,justifyContent:'center'}} >
         
             <img
            src={config.urls.product + "images/" + product?.image}
            width='350'
            height='350' />
           </Grid>
        </Grid>
        <Paper variant ='outlined'>
            <Typography color='inherit' align='center' variant='h4' component='h1'>
              רכישה 
              {console.log(props.match.params.id)}
            </Typography>
            <Stepper activeStep={activeStep}>
             {
                 steps.map((lable)=><Step key ={lable}><StepLabel>{lable}</StepLabel></Step>)
             }
            </Stepper>
            <React.Fragment>
            {activeStep === steps.length ? (
              <React.Fragment>
                <Typography variant="h5" gutterBottom>
                 תודה רבה על הזמנה שלך
                </Typography>
                <Typography variant="subtitle1">
                  הזמנה שלך נלקטה למערכת בהצלחה
                  מספר ההזמנה שלך היא 232345545
                  ברגע זה נשלח אלייך הודעה  על מנת לעקוב אחרי השינוי בהזמנה שלך
                </Typography>
              </React.Fragment>
            ) : (
              <React.Fragment>
                {getStepContent(activeStep)}
                <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
                  {activeStep !== 0 && (
                    <Button onClick={handleBack}>
                      אחורה
                    </Button>
                  )}
                  <Button
                    variant="contained"
                    onClick={handleNext}
                  >
                    {activeStep === steps.length - 1 ? 'שלח' : 'הבא'}
                  </Button>
                </Box>
              </React.Fragment>
            )}
          </React.Fragment>

        </Paper>

    </Container>
    </ThemeProvider>
    );
}