import * as React from 'react';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import { RouteComponentProps } from 'react-router-dom';
import productModel from '../../../Modules/productModel';
import { useEffect } from 'react';
import axios from 'axios';
import config from '../../../Configs/Configs'

interface RouterParams {
  id: string;
}
interface checkOutProps extends RouteComponentProps<RouterParams> {
}

export default function Review(props) {
  const addressFromDetails = props.addressFromDetails;
  const [product ,setProduct] = React.useState<productModel | null >()
  useEffect(()=>{
    axios.get(config.urls.product + addressFromDetails[1])
    .then((res)=>{setProduct(res.data)}).catch((err)=>{alert(err)})
  },[])
    return (
      <React.Fragment>
        <Typography variant="h6" gutterBottom>
          סיכום הזמנה
         
        </Typography>
        <Grid container>

          <Grid item xs ={6} sx={{display:'flex' ,justifyContent:'flex-start'}}>
           <Typography sx={{fontSize:'20px'}}>{product?.name}</Typography>
          </Grid>
          <Grid item  xs ={6} sx ={{py:2 , px:0}}>
           <Typography>{product?.price}שח</Typography>
          </Grid>
          <Grid item xs ={6} sx ={{py:2 , px:0}}>
            <Typography>משלוח</Typography>
          </Grid>
          <Grid item xs ={6 }  sx ={{py:2 , px:0}}>
            <Typography>חינם</Typography>
          </Grid>
          <Grid item xs ={6}  sx ={{py:2 , px:0}}>
            <Typography sx ={{fontSize:'20px'}}>סה"כ לתשלום</Typography>
          </Grid>
          <Grid item xs ={6}  sx ={{py:2 , px:0}}>
            <Typography sx ={{fontSize:'20px'}}>{product?.price}שח</Typography>
          </Grid>
        </Grid>

        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <Typography variant="h6" gutterBottom sx={{ mt: 2 ,fontFamily:"Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif"}}>
               פרטים אישים
            </Typography>
           <Typography sx={{py:2,px:0}}>
             שם מלא {addressFromDetails[2][0]}   {addressFromDetails[2][1]}
           </Typography>
           <Typography sx={{py:2,px:0}}>
             כתובת  {addressFromDetails[2][2]}   {addressFromDetails[2][3]}   {addressFromDetails[2][4]}
           </Typography>
           
          </Grid>
          <Grid item container direction="column" xs={12} sm={6}>
            <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
              פרטי הכרטיס
            </Typography>
            <Typography>
              סוג הכרטיס
            </Typography>
            <Typography>
              בעל הכרטיס
            </Typography>
            <Typography>
              מספר הכרטיס
            </Typography>
            <Typography>
              תוקף
            </Typography>
            <Grid container>

            </Grid>
          </Grid>
        </Grid>
      </React.Fragment>
    );
  }