import "./Earings.css";
import axios from "axios";
import { useEffect, useState } from "react";
import config from "../../../Configs/Configs";
import productModel from "../../../Modules/productModel";
import setHeaders from "../../../Services/setHeaders";
import RecipeReviewCard from "../UseCard/UseCard";

function Earings(): JSX.Element {
    const headers = setHeaders();
    const [product ,setProduct] =useState<productModel[]>();
    useEffect(()=>{
     axios.get(config.urls.earings ,{headers:headers}).then((res)=>{
       setProduct(res.data)
     }).catch((err)=>{alert(err)})
    },[])
    return (
        <div className="Chains">
			{
                product?.map(p=><RecipeReviewCard product={p} key={p.id} />)
            }
        </div>
    );
}

export default Earings;
