import * as React from 'react';
import { styled } from '@mui/material/styles';
import RadioGroup, { useRadioGroup } from '@mui/material/RadioGroup';
import FormControlLabel, {
  FormControlLabelProps,
} from '@mui/material/FormControlLabel';
import Radio from '@mui/material/Radio';

interface StyledFormControlLabelProps extends FormControlLabelProps {
  checked: boolean;
}

const StyledFormControlLabel = styled((props: StyledFormControlLabelProps) => (
  <FormControlLabel {...props} />
))(({ theme, checked }) => ({
  '.MuiFormControlLabel-label': checked && {
    color: theme.palette.primary.main,
  },
}));

function MyFormControlLabel(props: FormControlLabelProps) {
  const radioGroup = useRadioGroup();

  let checked = false;

  if (radioGroup) {
    checked = radioGroup.value === props.value;
  }

  return <StyledFormControlLabel checked={checked} {...props} />;
}

export default function UseRadioGroup(props) {
  const [delivery ,setDelivery] = React.useState<string>('first');
  const [cost ,setCost] = React.useState<number>(25);
  const deliveryHandler1=(e)=>{setDelivery(e.target.value)}
  const deliveryHandler2=(e)=>{setDelivery(e.target.value)}
  React.useEffect(() => {
    if (delivery === 'first') {
      setCost(0)
    }
    if (delivery === 'second') {
      setCost(25)
    }
  });
  const setCostToParent =()=>{
    props.parentCallBack(cost)
  }
  return (
    <RadioGroup name="use-radio-group" defaultValue="first">
      <MyFormControlLabel onChange ={deliveryHandler1} onClick ={setCostToParent} value="first" label={"שליח עד הבית 5-7 ימ עסקים בתוספת של :"    +"      " + 25 + "שח"} control={<Radio />} />
      <MyFormControlLabel onChange ={deliveryHandler2} onClick ={setCostToParent} value="second" label="דאור ישראל" control={<Radio />} />
    </RadioGroup>
  );
}