import axios from "axios";
import { useEffect } from "react";
import { useState } from "react";
import config from "../../../Configs/Configs";
import productModel from "../../../Modules/productModel";
import setHeaders from "../../../Services/setHeaders";
import Similar from "../Similar/Similar";
import RecipeReviewCard from "../UseCard/UseCard";
import "./Clocks.css";

function Clocks(): JSX.Element {
    const headers = setHeaders();
    const [product ,setProduct] =useState<productModel[]>();
    useEffect(()=>{
     axios.get(config.urls.clocks ,{headers:headers}).then((res)=>{
       setProduct(res.data)
     }).catch((err)=>{alert(err)})
    },[])
    return (
        <div className="Clocks">
			{
                product?.map(p=><RecipeReviewCard product={p} key={p.id} />)
            }
        </div>
    );
}

export default Clocks;
