import { Button, createStyles, makeStyles, Theme } from "@material-ui/core";
import axios from "axios";
import { useState } from "react";
import config from "../../../Configs/Configs";
import "./Cart.css";
import productModel from "../../../Modules/productModel";
import { useEffect } from "react";
import RecipeReviewCard from "../UseCard/UseCard";
import { useHistory } from "react-router-dom";
import { cleanup } from "@testing-library/react";
import { width } from "@mui/system";
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
      root:{
      boxShadow:'rgba(0,0,0,0.35) 0px,5px,15px',
      justifyContent:'center',
      alignItems:'center',
      display:'inline-block',
      },
      span:{
          fontSize:'20px'
      },
      payment:{
        width:'100%',
        height:'100px',
        display:'flex',
        justifyContent:'center',
        alignItems:'center' 
         },
         paymentButton:{
             background:'#FAE5D3',
             margin:'20px',
             width:'100px'
         }
    }))
function Cart(): JSX.Element {
    const classes = useStyles();
    const history = useHistory();
    const[userProduct ,setUserProduct] = useState<productModel[]>();
    useEffect(()=>{
            axios.get(config.urls.cart + "list").then((res)=>{
                if(res.data.msg){
                    return
                }
                setUserProduct(res.data);
               })
       

    },[])

    const renderToPayment =()=>{
        history.push('/payment')
    }
    const renderToProducts =()=>{
        history.push('/products')
    }

    return (
        <div className={classes.root}>
            <div>
            {userProduct?.map(p=><RecipeReviewCard product={p} key={p.id}  />)}
            </div>
           <div className ={classes.payment}>
               <Button className ={classes.paymentButton} onClick ={renderToProducts}>המשך קנייה</Button>
               <Button className ={classes.paymentButton} onClick={renderToPayment}>רכישה</Button>
           </div>
        </div>
    );
}

export default Cart;
