import * as React from 'react';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';

export default function PaymentForm() {
  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        תשלום
      </Typography>
      <Grid container spacing={3}>
        <Grid item xs={12} md={6}>
          <TextField
            required
            id="שם בעל הכרטיס"
            label="שם בעל הכרטיס"
            fullWidth
            autoComplete="cc-name"
            variant="filled"
          />
        </Grid>
        <Grid item xs={12} md={6}>
          <TextField
            required
            id="מספר הכרטיס"
            label="מספר הכרטיס"
            fullWidth
            autoComplete="cc-number"
            variant="filled"
          />
        </Grid>
        <Grid item xs={12} md={6}>
          <TextField
            required
            id="תוקף"
            label="תוקף"
            fullWidth
            autoComplete="cc-exp"
            variant="filled"
          />
        </Grid>
        <Grid item xs={12} md={6}>
          <TextField
            required
            id="cvv"
            label="CVV"
            helperText="שלוש ספרות בגב הכרטיס"
            fullWidth
            autoComplete="cc-csc"
            variant="filled"
          />
        </Grid>
        <Grid item xs={12}>
          <FormControlLabel
            control={<Checkbox color="secondary" name="saveCard" value="yes" />}
            label="שמור פרטי הכרטיס לפעם הבא"
          />
        </Grid>
      </Grid>
    </React.Fragment>
  );
}