import { Button, createStyles, makeStyles, TextField, Theme } from "@material-ui/core";
import axios from "axios";
import { useState } from "react";
import config from "../../../Configs/Configs";
const useStyles = makeStyles((theme:Theme)=>createStyles({
root:{
    width:'500px',
    marginTop:'20px',
    marginRight:'250px',
    margin:'20px',
    textAlign:'center',
    border:'1px solid black',
    borderRadius: '20px',
    background:'#F2F3F4 '
},
textField:{
    width:'400px',
    color:'white'
},
button:{
    width:'100px',
    color:'white',
    background:'#1B2631 '

}

    

    
}));

function Register(): JSX.Element {
    const classes = useStyles();
    const [username, setUserName] = useState();
    const [email, setEmail] = useState();
    const [password, setPassword] = useState();
    const usernameHandler = (event) => {
        if (!event.target.value) {
            return;
        }
        setUserName(event.target.value);
    }
    const emailHandler = (event) => {
        if (!event.target.value) {
            return;
        }
        setEmail(event.target.value);
    }
    const passwordHandler = (event) => {
        if (!event.target.value) {
            return;
        }
        setPassword(event.target.value);
    }
    async function registerHandler(){
        if(username!=null && password!=null && email!=null){
      
            await axios.post(config.urls.login + "register" ,{username,email,password}).then((res)=>{
               // sessionStorage.setItem('user', JSON.stringify(res.data.response));
               // sessionStorage.setItem('benta',JSON.stringify( res.data.response));
                sessionStorage.setItem('token',JSON.stringify( res.data.response.token));

            })
        }

    }

    return (
        <div className={classes.root}>
            <h1>register</h1>
            <TextField variant="outlined" className={classes.textField} label="username" type="text" placeholder="username" required onChange={usernameHandler} />
            <br />
            <br />
            <br />
            <TextField variant="outlined" className={classes.textField}label="email" type="text" placeholder="email" required onChange={emailHandler} />
            <br />
            <br />
            <br />
            <TextField variant="outlined" className={classes.textField} label="password" type="password" placeholder="password" required onChange={passwordHandler}/>
                <br />
                <br />
                <br />
                <Button className={classes.button} onClick ={registerHandler}>register</Button>

        </div>
            );
}
export default Register;
