import { Button, Card, CardActions, CardContent, CardHeader, createStyles, makeStyles, TextField, Theme } from "@material-ui/core";
import { useEffect, useState } from "react";
import axios from "axios";
import config from "../../../Configs/Configs";
import { Redirect, useHistory } from "react-router-dom";
import setHeader from "../../../Services/setHeaders"
import "./Login.css"
const useStyles = makeStyles((theme:Theme)=>
createStyles({
  root:{
    display:'inline-flex',
    textAlign:'center',
    margin:'30px',
    padding:'30px',
    marginRight:'300px',
    marginBottom:'30%',
    width:'500px',
    border:'1px solid black',
    borderRadius: '20px',
    background:'#F2F3F4 '
    
  },
  form:{
    marginRight:'40px',
    marginTop:'20px'
  },
  textField:{
    width:'400px',
    textAlign:'center'
    
  },
  span:{
    fontSize:'20px',
    color:'red'
  }
  
}))



function Login(): JSX.Element {
  const header = setHeader();
  const classes = useStyles();
  const [username ,setUserName] = useState<string>();
  const [password ,setPassword] = useState<string>();
  const history = useHistory();
  const formData = new FormData();
  formData.append('username',username);
  formData.append('password',password);
  async function rendering(e) {
      if (username === '' || username === undefined || password === '' || password === undefined){
        document.getElementById("validation").innerHTML = "these fields are required";
            setPassword('');
            setUserName('');
            return;
      }
      else{
      await axios.post(config.urls.login ,{username,password} ,{headers :{'Access-Control-Allow-Origin':header ,mode:'cors' }}).then((res) => {
                if (res.data.msg === "wrong") {
                  document.getElementById("validation").innerHTML = "incorrect username or password ";
                  setPassword('');
                  setUserName('');
                  return;
                }
                if (res.data.msg === "write") {
                  sessionStorage.setItem('token', res.data.response.token);
                  history.push("/admin-page")
                }
      
              });
            }
    }
  function userNameHandler(e) { setUserName(e.target.value); }
  const passwordHandler = (e) => {setPassword(e.target.value)}
  return (
    <div className="Login">
      <form method="POST"  >
            <TextField
             className="textField" label="Username"  placeholder="Username"
              onChange={userNameHandler}  variant="outlined"  required />
            <TextField
             className="textField" type="password" label="Password"
              placeholder="Password"  onChange={passwordHandler} variant="outlined" required   /> 
              < br />
              < br />
            <Button onClick={rendering} id="button" onKeyPress ={rendering}
            >Login
            </Button>
            < br />
            < br />
            <span className={classes.span} id ="validation"></span>
      </form>
      </div>
  );
}


export default Login;
