import { Button, createStyles, makeStyles, MenuItem, TextField, Theme } from "@material-ui/core";
import { NavLink, Redirect, useHistory} from "react-router-dom";
import { useEffect, useState } from "react";
import axios from "axios";
import config from "../../../Configs/Configs";
import setHeaders from "../../../Services/setHeaders";



const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
        direction:'rtl',
        margin:'10px',
        display:'grid',
        gridTemplateColumns:'repeat (6 ,ifr)'
        
    },
    header:{
       height:'100px',
       width:'82.5%',
       marginTop:'20px',
       marginRight:'10px',
       textAlign:'center',
       gridColumn:'span 6'

    },
    navLink:{
        margin:'20px',
        textDecoration:'none',
        color:'black',
        fontSize:'25px',
        fontFamily:'Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif',

    },
    add:{
       textAlign:'center',
       marginLeft:'100px',
       display:'inline-block',
       marginRight:'10px',
       marginTop:'20px',
       height:'500px',
       gridColumn:'span 3'



       
    },
    update:{
        textAlign:'center',
       display:'inline-block',
       marginTop:'20px',
       height:'500px',
       gridColumn:'span 3'





    },
    delete:{
            textAlign:'center',
            marginRight:'10px',
            display:'inline-block',
            marginTop:'20px'


    },
    button:{
        width:'100px',
        background:'cadetblue',
        marginTop:'80px'
        
    },
    textField:{
        width:'400px'
    },
    formOne:{
        width:'450px',
        border:'1px solid #D5DBDB ',
        borderRadius :"4px",
        

    },
    title:{
        fontFamily:'Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif',
        
    }
  }))
function AdminPage(): JSX.Element {
    const classes = useStyles();
    const [name ,setProductName] =useState();
    const [price ,setProductPrice] =useState();
    const [stock ,setProductStock] =useState();
    const [category ,setCategory] =useState();
    const [image ,setProductImage] =useState();
    const [description ,setDescription] =useState();
    const [productNameToUpdate,setProductNameToUpdate]=useState()
    const [product,setProduct] = useState()
    const productNameHandler = (e) => { setProductName(e.target.value) }
    const productPriceHandler = (e) => { setProductPrice(e.target.value) }
    const productStockHandler = (e) => { setProductStock(e.target.value) }
    const selectHandler = (e) => { setCategory(e.target.value) }
    const productDescriptionHandler = (e)=>{setDescription(e.target.value)}
    const productImageHandler = (e) => { setProductImage(e.target.files[0]) }
    async function addProductHandler() {
        const setHeader = setHeaders();
        const formData = new FormData();
        formData.append('image', image);
        formData.append('name', name);
        formData.append('price', price);
        formData.append('stock', stock);
        formData.append('description', description);
        formData.append('category', category);
        await axios.post(config.urls.product, formData,{headers:setHeader}).catch((err)=>{console.log(err)})
       document.getElementById('response').innerHTML ='המוצר הוספ בהצלחה'

        
    }
    const productNameToUpdateHandler =(event)=>{
      setProductNameToUpdate(event.target.value)
    }
   
    const selectCategory = [
        { key: 1, value: 'טבעת' },
        { key: 2, value: 'צמיד' },
        { key: 3, value: 'שעון' },
        { key: 4, value: 'עגיל' },
        { key: 5, value: 'שרשרת' },
    ]
    return (
        <div className={classes.root}>
            <div className={classes.header}>
                <NavLink to ="" className={classes.navLink}>ניהול הזמנות</NavLink>
                <NavLink to ="" className={classes.navLink}>ניהול מוצרים</NavLink>
                <NavLink to ="" className={classes.navLink}>ניהול ספקים</NavLink>


            </div>
           <div className = {classes.add}>
                <form className={classes.formOne} encType="multipart/form-data">
                    <h1 className={classes.title}>הוספת מוצר </h1>
                    <TextField
                        onChange={productNameHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="שם המוצר"
                        variant="outlined" /> <br /> <br />
                    <TextField
                        onChange={productPriceHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="מחיר המוצר"
                        variant="outlined" /><br /> <br />
                    <TextField
                        onChange={productStockHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="כמות במלאי"
                        variant="outlined" >
                    </TextField><br /> <br />
                    <TextField
                        onChange={productDescriptionHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="תיאור מוצר"
                        variant="outlined" >
                        rows={} 
                        multiline
                    </TextField><br /> <br />
                    <TextField
                        onChange={selectHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="קטגוריה"
                        variant="outlined"
                        select
                        name='select' >
                        {selectCategory.map(p => <MenuItem key={p.key} value={p.key}>{p.value}</MenuItem>)}
                    </TextField><br /> <br />
                    <TextField
                        onChange={productImageHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="תמונה"
                        variant="outlined"
                        type="file"
                        name='image' /><br /> <br />
                 
                    <h2 id="response"></h2>
                    <br />
                    <br />

                    <Button onClick={addProductHandler} className={classes.button}>הוסיף</Button><br /> <br />
                </form>
                
                
           </div>
          
          < div className = {classes.update}>
          <form className={classes.formOne} encType="multipart/form-data">
                    <h1 className={classes.title}>עדכן מוצר </h1>
                    <h4>שם מוצר לעדכון </h4>
                    <TextField variant="outlined" onChange ={productNameToUpdateHandler} />
                    <TextField
                        onChange={productNameHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="שם המוצר"
                        variant="outlined" /> <br /> <br />
                    <TextField
                        onChange={productPriceHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="מחיר המוצר"
                        variant="outlined" /><br /> <br />
                    <TextField
                        onChange={productStockHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="כמות במלאי"
                        variant="outlined" >
                    </TextField><br /> <br />
                    <TextField
                        onChange={productDescriptionHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="תיאור מוצר"
                        variant="outlined" >
                        rows={} 
                        multiline
                    </TextField><br /> <br />
                    <TextField
                        onChange={selectHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="קטגוריה"
                        variant="outlined"
                        select
                        name='select' >
                        {selectCategory.map(p => <MenuItem key={p.key} value={p.key}>{p.value}</MenuItem>)}
                    </TextField><br /> <br />
                    <TextField
                        onChange={productImageHandler}
                        className={classes.textField}
                        id="outlined-basic"
                        label="תמונה"
                        variant="outlined"
                        type="file"
                        name='image' /><br /> <br />
                 
                    <h2 id="response"></h2>
                    <br />
                    <br />

                    <Button onClick={addProductHandler} className={classes.button}>עדכן</Button><br /> <br />
                </form>
                
           </div>
          
        </div>
    );
}

export default AdminPage;
