import { Redirect, Route, Switch } from "react-router-dom";
import AboutUs from "../../AboutUs/AboutUs/AboutUs";
import Home from "../../Home/Home/Home";
import Page404 from "../Page404/Page404";
import Loadable from "react-loadable"
import Loading from "../../ShareArea/Loading/Loading";
import Login from "../../Log/Login/Login";
import AdminPage from "../../Home/AdminPage/AdminPage";
import Cart from "../../Product/Cart/Cart";
import Register from "../../Log/Register/Register";
import Forum from "../../Home/Forum/Forum";
import ContactUs from "../../ContactUsArea/ContactUs/ContactUs";
import AdminRoute from "../../ShareArea/protectedRouts/adminRoute";
import productList from "../../Product/product/productList";
import AdminLogDialog from "../../ShareArea/AdminLogDialog/AdminLogDialog";
import Clocks from "../../Product/Clocks/Clocks";
import Rings from "../../Product/Rings/Rings";
import Chains from "../../Product/Chains/Chains";
import Bracelets from "../../Product/Bracelets/Bracelets";
import Earings from "../../Product/Earings/Earings";
import Payment from "../../Product/Payment/Payment";
import { useEffect, useRef, useState } from "react";
import Ads from "../../Home/Ads/Ads";
import CheckOut from "../../Product/CheckOut/CheckOut";

function Routing(): JSX.Element {
    return (
        <div>
			<Switch>
                <Route path ="/" component ={Home } exact />
                <Route path ="/checkout/:id" component ={CheckOut } exact />
                <Route path ="/home" component ={Home } exact />
                <Route path ="/ads" component ={Ads } exact />
                <Route path ="/contact-us" component ={ContactUs} exact />
                <Route path ="/about-us" component ={AboutUs} exact  />
                <Route path ="/products" component ={Loadable({
                    loader:()=> import("../../Product/product/productList"),
                    loading:Loading
                })} exact />
                {/* <AdminRoute exact path ="/products" component={productList} /> */}
                {/* <AdminRoute exact path ="/products/payment" component={Payment} /> */}
                <Route path="/products/cart/:id" component ={Cart} exact />
                <Route path="/products/buy/:image&&:id" component ={Loadable({loader:()=>import("../../Product/Buy/Buy"),loading:Loading})} exact />
                {/* <Route path ="/admin-page" component ={AdminPage} exact /> */}
                <Route path ="/login" component ={Login} exact  />
                <Route path="/register" component ={Register } exact />
                <Route path="/forum" component ={Forum } exact />
                <Route path ="/cart" component={Cart} />
                <Route path ="/clocks" component={Clocks} />
                <Route path ="/rings" component={Rings} />
                <Route path ="/chains" component={Chains} />
                <Route path ="/bracelets" component={Bracelets} />
                <Route path ="/earings" component={Earings} />
                <Route path ="/payment/:image&&:id" component ={Payment} />
                <AdminRoute exact path ="/admin-page" component={AdminPage} />
                <Route exact path="/cart-dialog" component ={AdminLogDialog} />
                <Route component = {Page404} />
                

            </Switch>
        </div>
    );
}

export default Routing;
