import { Button, Checkbox, MenuItem, Radio, RadioGroup, Select, styled, TextField, useRadioGroup } from "@material-ui/core";
import axios from "axios";
import { Component, useState } from "react";
import { RouteComponentProps } from "react-router";
import productModel from "../../../Modules/productModel";
import "./Payment.css";
import config from "../../../Configs/Configs";
import UseRadioGroup from "../../Home/UseRadioGroup";
interface RouterParams{image:string,id:string}
interface paymentProps extends RouteComponentProps<RouterParams>{}
interface paymentState {
    product:productModel
    size:undefined
    count:number
    cost:number
}
class Payment extends Component<paymentProps, paymentState>

{
     selectSize = [
        { key: 1, value: 'x-small' },
        { key: 2, value: 'small' },
        { key: 3, value: 'medium' },
        { key: 4, value: 'large' },
        { key: 5, value: 'x-large' },
    ]
    
  public constructor(props:paymentProps){
      super(props);
      this.state ={
          product:null,
          size:null,
          count:1,
          cost:25
      }
  }
  public selectHandler =(e)=>{
      this.setState({size:e.target.value})
  }
 
  public async  componentDidMount(){
      const response =await axios.get<productModel>(config.urls.product + this.props.match.params.id);
      this.setState({product:response.data});
  }
  public  parentCallBack =(cost)=>{this.setState({cost:cost})}
  public sendOrders(){
            document.getElementById('sendOrder').innerHTML ="כל הפרטים נשלחו בהצלחה"
  }
  public render():JSX.Element{
      return(
          <div className="Payment">
              <div className="title">
                  <img src={config.urls.product + "images/" + this.props.match.params.image}  className="first-image" />
                  <img src={config.urls.product + "images/" + this.props.match.params.image} className ="second-image" />
                  <img src={config.urls.product + "images/" + this.props.match.params.image} className ="second-image"/>
              </div>
              <div className="body" >
                  <div className="formPayment">
                      <h2 className="titleDelivery"> כתובת למשלוח</h2>
                      <hr />
                      <form >
                          <TextField variant="filled" label="שם פרטי" required className="city" />
                          <TextField variant="filled" label="שם משפחה " required className="zone" />
                          <TextField variant="filled" label="רחוב/מספר דירה" required className="city" />
                          <TextField variant="filled" label="מספר בית" required className="zone" />
                          <TextField variant="filled" label="עיר" required className="city" />
                          <TextField variant="filled" label="מיקוד" required className="zone" />
                          <TextField variant="filled" label='דוא"ר' required className="city" />
                          <TextField variant="filled" label="טלפון" required className="zone" />
                      </form>
                  </div>
                  <div className="productInfo">
                      <div>
                          <h2> פרטי משלוח</h2>
                          <hr />
                      </div>
                      <div>
                          <p className="description">{this.state.product?.description}</p>
                      </div>
                      <div className="selectSizeCount">
                          <div className="selectSize">
                              <h3>בחר מידה רצויה </h3>
                              <TextField onChange={this.selectHandler}
                                  id="outlined-basic" label="מידה" variant="filled"
                                  select name='select' >
                                  {this.selectSize.map(p => <MenuItem key={p.key} value={p.key}>{p.value}</MenuItem>)}
                              </TextField><br /> <br />
                          </div>
                          <div className="selectCount">
                              <h3 >בחר כמות</h3>
                              <TextField type="number"  label ="כמות" variant ="filled" onChange={
                                  (e)=>{this.setState({count:+e.target.value})}} />
                          </div>
                          <div className="delivery">
                              <h3>משלוח</h3>
                              <UseRadioGroup parentCallBack ={this.parentCallBack} />
                          </div>
                          <div className="final-payment">
                              <div className ="final-payment-child1">
                              <h4>סהכ לתשלום:</h4>
                              </div>
                              <div className ="final-payment-child2">
                                <h4>{this.state.count ===0? this.state.product?.price :
                                +this.state.product?.price * this.state.count + this.state.cost}שח</h4>
                             </div>
                          </div>

                      </div>
                  </div>

              </div>
              <div className="footer">
                  <div>
                      <hr />
                      <h2>אפשרות תשלום</h2>
                  </div>
                  <div className="payMotheds">
                      <div className="cridt-card">
                          <h3>תשלום:כרטיס אשראי</h3>
                          <form>
                              <TextField className="user-id" label='מספר זהות' variant='filled' />
                              <TextField className="user-card-num" label='מספר הכרטיס' variant='filled' />
                              <TextField className="user-card-expired" label='תוקף' variant='filled' />
                              <TextField className="user-cvv" label='CVV' variant='filled' />

                          </form>

                      </div>
                      <div className="paypal">
                          <h3>תשלום:paypal</h3>
                          <form>
                              <TextField className="user-id2" label='מספר זהות' variant='filled' />
                              <TextField className="user-card-num2" label='מספר הכרטיס' variant='filled' />
                              <TextField className="user-card-expired2" label='תוקף' variant='filled' />
                              <TextField className="user-cvv2" label='CVV' variant='filled' />

                          </form>
                      </div>
                      <div className="payByPhone">
                          <div>
                          <h3>תשלום: טלפון</h3>
                          </div>
                         
                          <div className ="contact-phone">
                              <h3>03-8888-555</h3>
                              <h3>03-8888-553</h3>
                              <h3>03-8888-552</h3>
                          </div>
                      </div>
                  </div>
              </div>
              <div className ="submit-all-data">
                <Button onClick={this.sendOrders} >שלח</Button>
                <h2 id ="sendOrder"></h2>
              </div>
              <div>
              </div>
          </div>
      );
  }
    
    
  
}
    
    
export default Payment;
